"use client";

import { useState, useEffect } from "react";
import { ChevronLeft, ChevronRight, ZoomIn, ZoomOut, Loader2 } from "lucide-react";
import { Button } from "@/components/ui/button";

// We'll dynamically import react-pdf and pdfjs on the client to avoid server-side evaluation
// that can throw (DOMMatrix missing, worker mismatches, etc.).
let _workerObjectUrl: string | null = null;

async function ensurePdfWorker(pdfjs: any) {
  // Prefer a local bundled worker first (more reliable)
  try {
    const localUrl = '/pdf.worker.min.js';
    console.debug('Checking local worker at', localUrl);
    const localRes = await fetch(localUrl, { method: 'HEAD' });
    if (localRes.ok) {
      pdfjs.GlobalWorkerOptions.workerSrc = localUrl;
      try { if (pdfjs?.GlobalWorkerOptions) (pdfjs.GlobalWorkerOptions as any).disableWorker = false; } catch (e) { /* ignore */ }
      console.debug('Using local pdf.worker:', localUrl);
      return;
    }
  } catch (e) {
    console.debug('Local worker not available:', e);
  }

  // Try a few CDN strategies to obtain a worker script that matches the runtime pdfjs version.
  const tryUrls: string[] = [];
  const version = pdfjs?.version;
  if (version) tryUrls.push(`https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${version}/pdf.worker.min.js`);
  // fallback known stable versions
  tryUrls.push("https://cdnjs.cloudflare.com/ajax/libs/pdf.js/5.4.296/pdf.worker.min.js");
  tryUrls.push("https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.16.105/pdf.worker.min.js");

  for (const cdnUrl of tryUrls) {
    try {
      console.debug("Attempting to fetch pdf.worker from:", cdnUrl);
      const res = await fetch(cdnUrl);
      if (!res.ok) {
        console.debug("Worker fetch returned non-ok status", res.status, cdnUrl);
        continue;
      }
      const js = await res.text();
      const blob = new Blob([js], { type: "application/javascript" });
      if (_workerObjectUrl) URL.revokeObjectURL(_workerObjectUrl);
      _workerObjectUrl = URL.createObjectURL(blob);
      pdfjs.GlobalWorkerOptions.workerSrc = _workerObjectUrl;
      try { if (pdfjs?.GlobalWorkerOptions) (pdfjs.GlobalWorkerOptions as any).disableWorker = false; } catch (e) { /* ignore */ }
      console.debug("pdf.worker loaded via blob URL from", cdnUrl);
      return;
    } catch (err) {
      console.debug("Failed to fetch worker from", cdnUrl, err);
      // try next
    }
  }

  // If we reach here, all fetch attempts failed. Disable worker mode safely.
  console.error("Failed to set up pdf.worker from CDN; falling back to disabled worker mode");
  try {
    if (pdfjs?.GlobalWorkerOptions) (pdfjs.GlobalWorkerOptions as any).disableWorker = true;
    else (pdfjs as any).disableWorker = true;
    console.debug("pdfjs worker disabled; using main-thread parsing fallback");
  } catch (e) {
    console.error("Failed to disable pdfjs worker:", e);
  }
}

interface PDFViewerProps {
  fileUrl: string;
}

export default function PDFViewer({ fileUrl }: PDFViewerProps) {
  const [numPages, setNumPages] = useState<number | null>(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [scale, setScale] = useState(1.0);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [fullData, setFullData] = useState<Uint8Array | null>(null);
  const [isFetchingFallback, setIsFetchingFallback] = useState(false);

  // Dynamic react-pdf / pdfjs module objects
  const [reactPdf, setReactPdf] = useState<any>(null);
  const [pdfjsModule, setPdfjsModule] = useState<any>(null);

  useEffect(() => {
    setPageNumber(1);
    setScale(1.0);
    setError(null);
    setFullData(null);
    setIsLoading(true);
    setIsFetchingFallback(false);

    // Dynamically import react-pdf on client only
    let mounted = true;
    (async () => {
      try {
        const mod = await import("react-pdf");
        const pdfjs = (await import("pdfjs-dist/build/pdf")) as any;
        if (!mounted) return;
        setReactPdf(mod);
        setPdfjsModule(pdfjs);
        // setup worker after we have pdfjs
        await ensurePdfWorker(pdfjs);
      } catch (err) {
        console.error("Failed to dynamically import react-pdf/pdfjs:", err);
        setError(String(err?.message || err));
      }
    })();

    return () => {
      mounted = false;
    };
  }, [fileUrl]);

  function onDocumentLoadSuccess({ numPages }: { numPages: number }) {
    setNumPages(numPages);
    setIsLoading(false);
  }

  async function handleLoadError(err: any) {
    console.error("PDF load error:", err);
    setError(String(err?.message || err));

    // Try fallback: fetch the full file as ArrayBuffer and pass to react-pdf
    if (isFetchingFallback || fullData) return;

    try {
      setIsFetchingFallback(true);
      const resp = await fetch(fileUrl, { credentials: "include" });
      if (!resp.ok) throw new Error(`Fallback fetch failed: ${resp.status}`);
      const ab = await resp.arrayBuffer();
      const uint = new Uint8Array(ab);
      setFullData(uint);
      setError(null);
      setIsLoading(true);
      // react-pdf will try again automatically when `file` prop changes
    } catch (e) {
      console.error("Fallback fetch error:", e);
      setError(String(e?.message || e));
    } finally {
      setIsFetchingFallback(false);
    }
  }

  const DocumentComp = reactPdf?.Document ?? null;
  const PageComp = reactPdf?.Page ?? null;

  return (
    <div>
      <div className="flex items-center justify-between p-3 border-b bg-gray-50">
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm" onClick={() => setPageNumber((p) => Math.max(1, p - 1))} disabled={pageNumber <= 1}>
            <ChevronLeft className="h-4 w-4" />
          </Button>
          <div className="text-sm text-muted-foreground">Halaman {pageNumber}{numPages ? ` / ${numPages}` : ""}</div>
          <Button variant="outline" size="sm" onClick={() => setPageNumber((p) => (numPages ? Math.min(numPages, p + 1) : p + 1))} disabled={numPages !== null && pageNumber >= numPages}>
            <ChevronRight className="h-4 w-4" />
          </Button>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm" onClick={() => setScale((s) => Math.max(0.5, s - 0.25))}>
            <ZoomOut className="h-4 w-4" />
          </Button>
          <Button variant="outline" size="sm" onClick={() => setScale((s) => Math.min(3, s + 0.25))}>
            <ZoomIn className="h-4 w-4" />
          </Button>
        </div>
      </div>

      <div className="relative">
        {isLoading && (
          <div className="absolute inset-0 flex items-center justify-center bg-gray-100 z-10">
            <div className="text-center">
              <Loader2 className="h-8 w-8 animate-spin text-amber-500 mx-auto mb-2" />
              <p className="text-sm text-gray-500">Memuat dokumen...</p>
            </div>
          </div>
        )}

        <div className="w-full flex justify-center p-6 bg-white">
          {DocumentComp && PageComp ? (
            <DocumentComp
              file={fullData ? { data: fullData } : fileUrl}
              onLoadSuccess={onDocumentLoadSuccess}
              onLoadError={handleLoadError}
              loading={null}
            >
              <PageComp pageNumber={pageNumber} scale={scale} width={800} />
            </DocumentComp>
          ) : (
            <div className="text-sm text-gray-500">Menyiapkan viewer...</div>
          )}
        </div>

        {error && (
          <div className="p-4 text-sm text-red-600">
            <div>Terjadi kesalahan saat memuat dokumen: {error}</div>
            {isFetchingFallback ? <div className="text-xs text-gray-500">Mencoba fallback download...</div> : null}
          </div>
        )}
      </div>
    </div>
  );
}
